import { Form, Input, Button } from 'antd';
import styled from '@emotion/styled';
import { useAuth } from 'hooks/useAuth';

export const Register = () => {
  const { register, isLoginLoading } = useAuth();

  return (
    <Form onFinish={register}>
      <Form.Item
        name="username"
        rules={[
          {
            required: true,
            message: '',
          },
        ]}
      >
        <Input placeholder="" type="text" />
      </Form.Item>
      <Form.Item
        name="password"
        rules={[
          {
            required: true,
            message: '',
          },
        ]}
      >
        <Input placeholder="" type="password" id="password" />
      </Form.Item>
      <Form.Item
        name={'cpassword'}
        rules={[{ required: true, message: '' }]}
      >
        <Input
          placeholder=""
          type="password"
          id="cpassword"
        />
      </Form.Item>
      <Form.Item>
        <LongButton
          htmlType="submit"
          loading={isLoginLoading}
          type="primary"
        >
          
        </LongButton>
      </Form.Item>
    </Form>
  );
};

const LongButton = styled(Button)`
  width: 100%;
`;
